.movieItem {
  position: relative;
  display: flex;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.15);
}
.img {
  flex: 0 0 180px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.body {
  padding: 12px 20px;
  display: flex;
  flex-direction: column;
  row-gap: 7px;
}
.title {
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
}
.date {
  color: #827e7e;
  font-size: 12px;
  font-weight: 400;
  line-height: 22px;
}
.genres {
  display: flex;
  flex-wrap: wrap;
  column-gap: 8px;
  row-gap: 4px;
}
.genre {
  padding: 2px 4px;
  border-radius: 2px;
  border: 1px solid #d9d9d9;
  background: #fafafa;
  color: rgba(0, 0, 0, 0.65);
  font-size: 12px;
  font-weight: 400;
}
.description {
  font-size: 12px;
  font-weight: 400;
  line-height: 22px;
}
.rate {
  position: absolute;
  bottom: 10px;
  right: 20px;
  font-size: 17px;
}
